import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { selectGroupTopInfoBean, infoList } from './bean/customRankList.bean';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import * as _ from 'lodash';
import { insertCustContactProgramBean, custContactList } from './bean/insertCustContactProgram.bean';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { custContactTaskDealBean } from './bean/custContactTaskDeal.bean';

@Component({
  selector: 'custom-ranking-list',
  templateUrl: './custom-ranking-list.component.html',
  styleUrls: ['./custom-ranking-list.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CustomRankingListComponent implements OnInit, OnChanges {
  @Input() groupId: any;
  // @Input() groupListDemo: any;
  items: any[];

  // 
  increaseInDeposit: any = {};//
  depositDecline: any = {};//
  loanDecline: any = {};//
  depositIncrease: any = {};//
  preFlag: boolean = false;//

  // bean
  selectGroupTopInfoBean: selectGroupTopInfoBean = new selectGroupTopInfoBean();//
  infoList: infoList = new infoList();
  insertCustContactProgramBean: insertCustContactProgramBean = new insertCustContactProgramBean();
  custContactTaskDealBean: custContactTaskDealBean = new custContactTaskDealBean();

  // 
  msgs: Message[] = [];//

  // 
  inValue: any = {};
  inList: any = [];
  detailDisplay: boolean = false;
  detailTitle: any = '';//
  first: any = 0;//
  num: any = '';

  // 
  Header: any = '';
  display: boolean = false;
  addUpHeader: any = '';
  // 
  addDisplay = true;
  custNoAdd: any = '';
  custNameAdd: any = '';
  list: any = [];
  orgId: string = '';
  userId: string = '';
  userName: string = '';
  roleName: string = '';
  orgName: any = '';
  inValue1: any = [];


  // ()
  dealDisplay: boolean = false;
  data: any = [];
  dealInValue: any = [];
  // 
  selectedValues1: any = [];
  selectedValues2: any = [];
  selectedValues3: any = [];
  selectedValues4: any = [];
  selectVal: any = {};


  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) { }

  ngOnInit() {
    this.preSet();//

    this.selectGroupTopInfo('11');
    this.selectGroupTopInfo('12');
    this.selectGroupTopInfo('21');
    this.selectGroupTopInfo('22');
  }
  ngOnChanges() {
    
  }
  preSet() {
    // 
    let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (!mesg) {
    }
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.orgName = ch['orgName'];//


    if (mesg.orgId) {
      this.orgId = mesg.orgId;
    }
    if (mesg.userId) {
      this.userId = mesg.userId;
    }
    if (ch.userName) {
      this.userName = ch.userName;
    }
    if (ch.roleName) {
      this.roleName = ch.roleName;
    }

    this.selectGroupTopInfoBean.groupId = this.groupId;//
    this.selectGroupTopInfoBean.pageNum = 1;
    this.selectGroupTopInfoBean.pageSize = 10;

    // this.increaseInDeposit = {
    //   // 
    //   pageNum: 1,
    //   pageSize: 10,
    //   groupId: '',
    //   type: '11',
    //   totalNum: 1,
    //   infoList: [
    //     {
    //       number: '1',
    //       custNo: '1',
    //       custName: '1',
    //       balance: '1',
    //     }
    //   ],
    // }
    // this.depositDecline = {
    //   // 
    //   pageNum: 1,
    //   pageSize: 10,
    //   groupId: '',
    //   type: '12',
    //   totalNum: 1,
    //   infoList: [
    //     {
    //       number: '2',
    //       custNo: '2',
    //       custName: '2',
    //       balance: '2',
    //     }
    //   ],
    // }
    // this.loanDecline = {
    //   // 
    //   pageNum: 1,
    //   pageSize: 10,
    //   groupId: '',
    //   type: '21',
    //   totalNum: 1,
    //   infoList: [
    //     {
    //       number: '3',
    //       custNo: '3',
    //       custName: '3',
    //       balance: '3',
    //     }
    //   ],
    // }
    // this.depositIncrease = {
    //   // 
    //   pageNum: 1,
    //   pageSize: 10,
    //   groupId: '',
    //   type: '22',
    //   totalNum: 1,
    //   infoList: [
    //     {
    //       number: '4',
    //       custNo: '4',
    //       custName: '4',
    //       balance: '4',
    //     }
    //   ],
    // }
  }
  reSet() {
    // 
    this.increaseInDeposit = {};//
    this.depositDecline = {};//
    this.loanDecline = {};//
    this.depositIncrease = {};//
    this.preFlag = false;//
    // 
    this.inValue = {};
    this.inList = [];
    this.detailDisplay = false;
    this.detailTitle = '';//
    this.first = 0;//
    this.num = '';

    // 
    this.Header = '';
    this.display = false;
    this.addUpHeader = '';
    // 
    this.addDisplay = true;
    this.custNoAdd = '';
    this.custNameAdd = '';
    this.list = [];
    this.orgId = '';
    this.userId = '';
    this.userName = '';
    this.roleName = '';
    this.orgName = '';
    this.inValue1 = [];


    // ()
    this.dealDisplay = false;
    this.data = [];
    this.dealInValue = [];
    // 
    this.selectedValues1 = [];
    this.selectedValues2 = [];
    this.selectedValues3 = [];
    this.selectedValues4 = [];
    this.selectVal = {};
    this.selectGroupTopInfoBean = new selectGroupTopInfoBean();//
    this.infoList = new infoList();
    this.insertCustContactProgramBean = new insertCustContactProgramBean();
    this.custContactTaskDealBean = new custContactTaskDealBean();
  }
  selectGroupTopInfo(i) {
    // 
    this.selectGroupTopInfoBean.groupId = this.groupId;//    
    this.selectGroupTopInfoBean.type = i;
    this.httpService.selectGroupTopInfo(this.selectGroupTopInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.selectGroupTopInfoBean = data;
        this.selectGroupTopInfoBean.infoList = data.infoList;
        if (this.selectGroupTopInfoBean) {
          this.inValue = _.clone(this.selectGroupTopInfoBean);
        }
        if (this.selectGroupTopInfoBean.infoList) {
          this.inList = _.clone(this.selectGroupTopInfoBean.infoList);
        }
        this.inValue.groupId = this.groupId;//

        // if (this.preFlag) {
        //   this.detailDisplay = true;//
        // }
        this.showList(i);
        // this.preFlag = true;//
      } else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }
  showList(i) {
    if (i == '11') {
      // 
      this.increaseInDeposit = _.clone(this.selectGroupTopInfoBean);
      this.increaseInDeposit.infoList = _.clone(this.selectGroupTopInfoBean.infoList);
    }
    if (i == '12') {
      // 
      this.depositDecline = _.clone(this.selectGroupTopInfoBean);
      this.depositDecline.infoList = _.clone(this.selectGroupTopInfoBean.infoList);
    }
    if (i == '21') {
      // 
      this.loanDecline = _.clone(this.selectGroupTopInfoBean);
      this.loanDecline.infoList = _.clone(this.selectGroupTopInfoBean.infoList);
    }
    if (i == '22') {
      // 
      this.depositIncrease = _.clone(this.selectGroupTopInfoBean);
      this.depositIncrease.infoList = _.clone(this.selectGroupTopInfoBean.infoList);
    }
  }
  detailShow(i) {
    this.num = i;
    this.inValue = {};
    this.inValue.pageSize = 1;
    this.inValue.pageSize = 10;
    this.inList = [];
    this.first = 0;
    if (i == '11') {
      // 
      this.detailTitle = '';
      this.selectGroupTopInfo(i);
    }
    if (i == '12') {
      // 
      this.detailTitle = '';
      this.selectGroupTopInfo(i);
    }
    if (i == '21') {
      // 
      this.detailTitle = '';
      this.selectGroupTopInfo(i);
    }
    if (i == '22') {
      // 
      this.detailTitle = '';
      this.selectGroupTopInfo(i);
    }
    this.detailDisplay = true;
    document.body.style.overflow = "hidden";
  }

  returnDetail(event) {
    // 
    this.detailDisplay = event.detailDisplay;
    document.body.style.overflow = 'auto';
  }
  checkBinary(event) {


  }
  infoFL(i) {
    // 
    this.selectVal = [];
    if (i === 1) {
      if (this.selectedValues1.length > 1) {
        this.msgs = [];
        this.msgs = [{ severity: 'info', summary: '', detail: '' }];
        return;
      }
      this.selectVal = _.clone(this.selectedValues1);
    }
    if (i === 2) {
      if (this.selectedValues2.length > 1) {
        this.msgs = [];
        this.msgs = [{ severity: 'info', summary: '', detail: '' }];
        return;
      }
      this.selectVal = _.clone(this.selectedValues2);
    }
    if (i === 3) {
      if (this.selectedValues3.length > 1) {
        this.msgs = [];
        this.msgs = [{ severity: 'info', summary: '', detail: '' }];
        return;
      }
      this.selectVal = _.clone(this.selectedValues3);
    }
    if (i === 4) {
      if (this.selectedValues4.length > 1) {
        this.msgs = [];
        this.msgs = [{ severity: 'info', summary: '', detail: '' }];
        return;
      }
      this.selectVal = _.clone(this.selectedValues4);
    }
  }
  //
  newPlan(i) {
    this.infoFL(i);
    this.list = [];
    if (!(this.selectVal && this.selectVal[0] && this.selectVal[0].custNo)) {
      this.msgs = [];
      this.msgs = [{ severity: 'info', summary: '', detail: '' }];
      return;
    }
    this.list[0] = {};
    this.list.forEach(item => {
      item.custNo = this.selectVal[0].custNo;
      item.custName = this.selectVal[0].custName;
    });
    this.addUpHeader = '';
    this.dealDisplay = false;
    this.display = true;
    this.addDisplay = true;
    this.inValue1 = this.list;
    document.body.style.overflow = "hidden";
  }

  //-
  newPlanCall(event) {
    this.addDisplay = false;
    this.display = false;
    this.msgs = event.msgs;
    document.body.style.overflow = 'auto';
    this.ngOnInit();
  }

  //
  dealTask(i) {
    this.infoFL(i);
    this.data = [];
    if (!(this.selectVal && this.selectVal[0] && this.selectVal[0].custNo)) {
      this.msgs = [];
      this.msgs = [{ severity: 'info', summary: '', detail: '' }];
      return;
    }
    this.data[0] = {};
    this.data.forEach(item => {
      item.custNo = this.selectVal[0].custNo;
      item.custName = this.selectVal[0].custName;
    });
    this.addUpHeader = '';
    this.addDisplay = false;
    this.display = true;
    this.dealDisplay = true;
    this.dealInValue = this.data;
    document.body.style.overflow = "hidden";
  }

  //
  dealCall() {
    this.dealDisplay = false;
    this.display = false;
    document.body.style.overflow = 'auto';
  }
  onHidden() {
    // 
    document.body.style.overflow = 'auto';
  }

  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
