        this.custGroupListBean.custNum = data.custNum;
        this.custGroupListBean.doorSum = data.doorNum;
        this.custGroupListBean.custList = data.custList;
        this.list = data.custList;
        this.list.forEach((item, i) => {
          this.list[i].creditFlag = this.list[i].creditFlag || '';
          if (this.list[i].targetCustFlag && this.list[i].targetCustFlag != '') {//
            if (this.list[i].targetCustFlag == 1) {
              this.list[i].targetCustFlag = '';
            } else if (this.list[i].targetCustFlag == 0) {
              this.list[i].targetCustFlag = '';
            }
          }
          if (this.list[i].effFlag && this.list[i].effFlag != '') {//
            if (this.list[i].effFlag == 1) {
              this.list[i].effFlag = '';
            } else if (this.list[i].effFlag == 0) {
              this.list[i].effFlag = '';
            }
          }
          if (!this.list[i].allocateContacts || this.list[i].allocateContacts == '0') {//
            this.list[i].allocateContacts = '';
          }
        });
        /**
         * 2018.1.9
         */
