  submitBtn() {
    let param = {
      groupId: this.groupId,
      custList: [{ custNo: this.custGroupListBean.custList[this.index_2].custNo }],
    }
    this.httpService.deleteCustGroupList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.selectCustGroupList(this.i);//
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'success', summary: '', detail: '' }];
      } else if (data.returnCode.code == 'CCP10014') {
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '', detail: ',],' }];
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }
  deleteCustList(index) {
    this.checkNum = 1;
    this.index_2 = index;
    // 
    this.waitSelUserList = this.commfunc.query('QXXBGLC', {});
    if (!this.waitSelUserList.erro) {
      this.outVal = this.waitSelUserList.waitSelUserList;
      let showthis = this.waitSelUserList.display;
      let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      let a = commonHeader.extParam.specifyNextOpers;
      if (showthis == true && (a == undefined || a == '' || a == null)) {
        this.aaaa = true;
      } else {
        this.submitBtn();
      }
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
    }
  }
