  checkBinary(event, i) {
    //  
    // 
    let inValTemp = []
    this.custGroupListBean.custList.forEach(item => {
      // if (item.custNo.search(/^QL/)) {
      inValTemp.push(item);
      // }
    })
    // 

    if (event) {
      this.cId.push(this.custGroupListBean.custList[i].custNo);
      this.cName.push(this.custGroupListBean.custList[i].custName);
    }
    else {
      let cIdArr = [];
      cIdArr = _.clone(this.cId);
      cIdArr.splice(i, 1);
      this.cId = cIdArr;
      let cNameArr = [];
      cNameArr = _.clone(this.cName);
      cNameArr.splice(i, 1);
      this.cName = cNameArr;
    }

    this.params = {
      // 
      groupId: this.groupId,
      groupName: this.groupName,
      cId: this.cId,
      cName: this.cName,
    }
    // 
    if (event && this.selectedValues.length == inValTemp.length && !this.flag) {
      this.changeSelect(true, 0);
    }
    else if (this.flag && !event) {
      this.changeSelect(false, 0);
    }
  }
  changeSelect(event, checkOne) {
    // 
    if (event) {
      let temArr = [];
      // 
      let inValTemp = [];
      this.custGroupListBean.custList.forEach(item => {
        // if (item.custNo.search(/^QL/)) {
        inValTemp.push(item);
        // }
      })
      // 
      let temArr2 = []
      temArr2 = _.clone(inValTemp);
      // let temArr2 = _.clone(this.custGroupListBean.custList);
      for (let i of temArr2) {
        temArr.push(i);
      }
      this.selectedValues = temArr;
      this.flag = true;
      this.selectedAllVal = []
      this.selectedAllVal.push(true)
      if (checkOne == 0) {
        // 
      }
      else if (checkOne == 1) {
        // 
        // 
        for (let i in temArr2) {
          this.cId.push(temArr2[i].custNo);
          this.cName.push(temArr2[i].custName);
        }
      }
    }
    else {
      this.flag = false;
      this.selectedAllVal = []
      this.selectedAllVal.push(false)
      if (checkOne == 0) {
        // 
      }
      else if (checkOne == 1) {
        // 
        // 
        this.cId = [];
        this.cName = [];
        // 
        let temArr = [];
        this.selectedValues = temArr;
      }
    }
    this.params = {
      // 
      groupId: this.groupId,
      groupName: this.groupName,
      cId: this.cId,
      cName: this.cName,
    }
  }
