  translateUrlParams(obj) {
    let flag = ';';
    for (let key in obj) {
      flag += (key + '=' + obj[key] + ';');
    }
    return flag;
  }
  translateUrlParams2(obj) {
    let flag = '{';
    for (let key in obj) {
      flag += ('"' + key + '":"' + obj[key] + '",');
    }
    flag = flag.substring(0, flag.length - 1);
    flag += '}';
    return flag;
  }
  //   
