  //
  submitBtnBulk() {
    let custom_id = [];
    this.selectedValues.forEach(custom => {
      custom_id.push(custom.custNo);
    });
    let param = {
      groupId: this.groupId,
      custNo: custom_id,
    }
    this.httpService.deleteCustGroupList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.selectCustGroupList(this.i);//
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'success', summary: '', detail: '' }];
      } else if (data.returnCode.code == 'CCP10014') {
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '', detail: ',],' }];
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }
