  moreDelete() {
    if (this.selectedValues.length < 1) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
    } else {
      this.checkNum = 3;
      // 
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers;
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.waitSelUserList = this.commfunc.query('QXXBGLC', {});
      if (!this.waitSelUserList.erro) {
        this.outVal = this.waitSelUserList.waitSelUserList;
        let chooseThis = this.waitSelUserList.display;
        console.log(this.aaaa);
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let a2 = commonHeader.extParam.specifyNextOpers;
        // console.log(a2);
        if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
          this.aaaa = true;
        } else {
          this.delMore();
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
      }
    }
  }
  //
  delMore() {
    let arr = [];
    if (this.selectedValues.length < 1) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
    } else {
      for (let item of this.selectedValues) {
        arr.push({ custNo: item.custNo })
      }
      let param = {
        groupId: this.groupId,
        custList: arr,
      }
      this.httpService.deleteCustGroupList(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
          if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
          }
          this.selectCustGroupList(this.i);//
          this.msgs = [];
          data.returnCode.code ? data.returnCode.code : '';
          this.msgs = [{ severity: 'success', summary: '', detail: '' }];
        } else {
          this.msgs = [];
          data.returnCode.code ? data.returnCode.code : '';
          this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
        }
      }, err => {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
      })
      console.log(this.cId + '         ' + arr)
    }
  }

  //----------------------20171201
  selectCustGroupInfo() {
    this.isHasDel = false;
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let parames = {
      groupId: this.groupId
    }
    // 
    // this.httpService.selectGroupAdminInfo(this.groupAdminInfoBean).subscribe(data => {
    this.httpService.selectCustGroupInfo(parames).subscribe(data => {//1201
      if (data.returnCode.code == 'success') {
        // console.log(data)
        this.orgId = data.orgId
        let stu_1 = false;
        let stu_2 = false;
        if (data.ownerId == commonHeader.userId) {
          stu_1 = true;
        }
        stu_2 = data.contactList.some((item) => {
          return item.roleId == commonHeader.userId;
        })
        if (stu_1 || stu_2) {
          this.isHasDel = true;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }

