import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from "primeng/components/common/message";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { tableMessage, CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { Router } from "@angular/router";

@Component({
    selector: 'group-evaluator-statistical',
    templateUrl: './group-evaluator-statistical.component.html',
    styleUrls: ['./group-evaluator-statistical.component.scss'],
})
export class GroupEvaluatorStatistical implements OnInit {
    @Input() inValue: any;
    msgs: Message[] = [];
    userform: FormGroup;
    message: any = { msg: [] };
    tabMesg: any = tableMessage;//
    code: any;
    first: number = 0;//
    pageNum: number = 1;//
    pageSize: number = 10;//
    totalNum: number;//
    dataList: any = [];
    groupTypeList: any;//
    statusIdList: any;//
    display: boolean = false;//
    orgTreeDisplay: boolean = false;//
    ch: any = CALENDAR_CN;//
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    constructor(
        public httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public router: Router,
    ) {
        this.userform = fb.group({
            orgId: [''],//
            groupType: [''],//
            partyClassificationGroupId: ['', [Validators.maxLength(20), specialSymbol]],//
            groupName: ['', [Validators.maxLength(255), specialSymbol]],//
            ownerName: ['', [Validators.maxLength(255), specialSymbol]],//
            createdStampStart: [''],//
            createdStampEnd: [''],//
        })
    }
    ngOnInit() {
        this.codeValues() //
        this.groupTypeList = this.code['GroupTypeCreate'];
        this.statusIdList = this.code['DiscountStatus'];
        this.queryClick();
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    // /
    openTree() {
        this.orgTreeDisplay = !this.orgTreeDisplay;
    }
    // -
    contacttreeCall(param) {
        this.orgTreeDisplay = false;
        this.userform.get('orgId').setValue(param.orgId);
    }
    // 
    showTime(param1, param2) {
        if (param1 == 'plan') {
            const createdStampStart = this.commfunc.transDateN(this.userform.value.createdStampStart);
            const createdStampEnd = this.commfunc.transDateN(this.userform.value.createdStampEnd);
            if (createdStampStart && createdStampEnd) {
                if (createdStampStart > createdStampEnd) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.userform.value.createdStampStart = null;
                        } else {
                            this.userform.value.planContactTimeTo = null;
                        }
                    })
                }
            }
        }
    }
    // 
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }
    //
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['createdStampStart'] = this.commfunc.transDateN(this.userform.value.createdStampStart);
        param['createdStampEnd'] = this.commfunc.transDateN(this.userform.value.createdStampEnd);
        this.httpService.groupAdminStatementQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    download() {
        if (this.dataList.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return;
        }
        const param = {};
        param['report'] = '01';
        this.httpService.groupAdminStatementExport(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.router.navigate(["pages/tzb/custom/downloadCenter/downCenter"])
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    // 
    reset() {
        this.userform.get('orgId').setValue('');
        this.userform.get('groupType').setValue('');
        this.userform.get('partyClassificationGroupId').setValue('');
        this.userform.get('groupName').setValue('');
        this.userform.get('ownerName').setValue('');
        this.userform.get('createdStampStart').setValue('');
        this.userform.get('createdStampEnd').setValue('');
    }
    paginate(event) {
        //
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
}