import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from "primeng/components/common/message";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { ActivatedRoute } from "@angular/router";

@Component({
    selector: 'group-evaluator-detail',
    templateUrl: './group-evaluator-detail.component.html',
    styleUrls: ['./group-evaluator-detail.component.scss'],
})
export class GroupEvaluatorDetail implements OnInit {
    msgs: Message[] = [];
    userform: FormGroup;
    tabMesg: any = tableMessage;//
    code: any;
    first: number = 0;//
    pageNum: number = 1;//
    pageSize: number = 10;//
    totalNum: number;//
    dataList: any = [];
    targetCustFlagList: any;//
    offerLinesList: any;//
    healthFlagList: any;//
    creditEvalList: any;//
    badHabitsList: any;//
    familyStableFlagList: any;//
    sourceOfIncomeList: any;//
    incomeLevelList: any;//
    carFlagList: any;//
    recordStatusList: any;//
    evalPartyId: any;
    evalPartyName: any;
    targetCustFlag: any;
    groupId: string;
    constructor(
        public httpService: CusOperationHttpService,
        public routerInfo: ActivatedRoute,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.evalPartyId = this.routerInfo.snapshot.params['evalPartyId'];
        this.evalPartyName = this.routerInfo.snapshot.params['evalPartyName'];
        this.targetCustFlag = this.routerInfo.snapshot.params['targetCustFlag'];
        this.groupId = this.routerInfo.snapshot.params['groupId'];
        this.userform = fb.group({
            partyId: ['', [Validators.maxLength(20), specialSymbol]],//
            custName: ['', [Validators.maxLength(255), specialSymbol]],//
            evalPartyId: ['', [Validators.maxLength(20), specialSymbol]],//
            evalPartyName: ['', [Validators.maxLength(255), specialSymbol]],//
            targetCustFlag: [''],//
            offerLines: [''],//
        })
    }
    ngOnInit() {
        this.codeValues() //
        this.targetCustFlagList = this.code['IsTargetCreditCust'];
        this.offerLinesList = this.code['GroupOfferLinese'];
        this.healthFlagList = this.code['GroupHealthFlag'];
        this.creditEvalList = this.code['GroupCreditEval'];
        this.badHabitsList = this.code['BadHobby'];
        this.familyStableFlagList = this.code['GroupFamilyStableFlags'];
        this.sourceOfIncomeList = this.code['GroupSourceOfIncome'];
        this.incomeLevelList = this.code['GroupIncomeLevel'];
        this.carFlagList = this.code['GroupCarFlag'];
        this.recordStatusList = this.code['StatusId'];
        this.userform.get('evalPartyId').setValue(this.evalPartyId || '');
        this.userform.get('evalPartyName').setValue(this.evalPartyName || '');
        this.userform.get('targetCustFlag').setValue(this.targetCustFlag || '');
        this.queryClick();
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['groupId'] = this.groupId;
        this.httpService.groupAdminDetailInfoQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    partydetail() {
        const param = {};
        param['custNo'] = this.userform.value.partyId;
        this.httpService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userform.get('custName').setValue(data.custName);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    // 
    reset() {
        this.userform.get('partyId').setValue('');
        this.userform.get('custName').setValue('');
        this.userform.get('evalPartyId').setValue('');
        this.userform.get('evalPartyName').setValue('');
        this.userform.get('targetCustFlag').setValue('');
        this.userform.get('offerLines').setValue('');
    }
    paginate(event) {
        //
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
    //
    return() {
        window.history.go(-1);
    }
}