import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute } from "@angular/router";
import { Message } from "primeng/components/common/message";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { specialSymbol } from "app/@uisftech/common/service/validator";

@Component({
    selector: 'group-evaluator',
    templateUrl: './group-evaluator.component.html',
    styleUrls: ['./group-evaluator.component.scss'],
})
export class GroupEvaluator implements OnInit {
    msgs: Message[] = [];
    userform: FormGroup;
    tabMesg: any = tableMessage;//
    code: any;
    first: number = 0;//
    pageNum: number = 1;//
    pageSize: number = 10;//
    totalNum: number;//
    dataList: any = [];
    statusIdList: any;//
    display: boolean = false;//
    showModel: any;//
    addUpHeader: any;//
    modifyValue: any;//
    roleTypefyList: any;//
    inValue: any = [];//
    groupId: any;
    roleType: any;
    constructor(
        public httpService: CusOperationHttpService,
        public routerInfo: ActivatedRoute,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public router: Router,
        public fb: FormBuilder,
    ) {
        this.roleType = this.routerInfo.snapshot.params['roleType'];
        this.groupId = this.routerInfo.snapshot.params['groupId'];
        this.userform = fb.group({
            userId: ['', [Validators.maxLength(20), specialSymbol]],//
            userName: ['', [Validators.maxLength(255), specialSymbol]],//
            statusId: ['1'],//
        })
    }
    ngOnInit() {
        this.codeValues() //
        this.statusIdList = this.code['statusIdgroup'];
        this.roleTypefyList = this.code['roleTypefy'];
        this.queryClick();
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['partyClassificationGroupId'] = this.groupId;
        this.httpService.selectGroupAdminDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    partydetail() {
        const param = {};
        param['custNo'] = this.userform.value.userId;
        this.httpService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userform.get('userName').setValue(data.userName);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    // 
    add() {
        this.inValue = '';
        this.display = true;
        this.modifyValue = 1;
        this.addUpHeader = '';
    }
    update(col) {
        this.inValue = col;
        this.display = true;
        this.modifyValue = 2;
        this.addUpHeader = '';
    }
    // 
    addCall(param) {
        this.display = param;
        this.pageNum = 1;
        this.queryClick();
    }
    //
    detail() {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view/group-evaluator-detail', { groupId: this.groupId }]);
    }
    //
    summary() {
        this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view/group-evaluator-summary', { partyClassificationGroupId: this.groupId }]);
    }
    //
    statistical() {
        this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view/group-evaluator-statistical']);
    }
    //
    orgstatistical() {
        this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view/group-org-statistical']);
    }
    //
    extension() {
        this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view/group-extension-statistical']);
    }
    //
    custom() {
        this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view/group-custom-statistical']);
    }
    //
    return(){
        let groupId = this.commfunc.compileStr(this.groupId);
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: groupId }]);
    }
    //
    // batch() {
    //     this.router.navigate(['pages/tzb/custom/custom-group/custom-group-view/group-evaluator-element']);
    // }
    // 
    reset() {
        this.userform.get('userId').setValue('');
        this.userform.get('userName').setValue('');
        this.userform.get('statusId').setValue('1');
    }
    paginate(event) {
        //
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
}