import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol, checkNumber } from "app/@uisftech/common/service/validator";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from "app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.constant";
import { tableMessage } from "app/@uisftech/common/constant/config";
import * as _ from 'lodash';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

@Component({
    selector: 'group-marketing-tion',
    templateUrl: './group-marketing-tion.component.html',
    styleUrls: ['./group-marketing-tion.component.scss'],
})
export class GroupMarketingTion implements OnInit {
    msgs: Message[] = [];
    userform: FormGroup;
    infoform: FormGroup;
    message: any = { msg: [] };
    tabMesg: any = tableMessage;//
    code: any;
    first: number = 0;//
    pageNum: number = 1;//
    pageSize: number = 10;//
    totalNum: number;//
    dataList: any = [];
    bussTypeList: any;//
    usageCategoryList: any;//
    statusIdList: any;//
    pointerTypeList: any;//
    isadd: boolean = false;//
    isupdate: boolean = false;//
    ch: any = CALENDAR_CN;//
    ispointer: boolean = false;//
    nopointer: boolean = false;//
    display: boolean = false;//
    addUpHeader: any;//
    dataDate: Date = new Date();
    constructor(
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.userform = fb.group({
            subdibisionId: ['', specialSymbol],//
            pointerType: ['0'],//
            endDateTime: [''],//
            startDateTime: [''],//
            // usageCategory: ['0'],//
            bussType: ['0'],//
            subdibisionName: ['', specialSymbol],//
        });
        this.infoform = fb.group({
            subdibisionId: [''],//
            pointerType: ['0'],//
            endDateTime: [''],//
            startDateTime: [''],//
            // usageCategory: ['0'],//
            bussType: ['0'],//
            subdibisionName: ['', specialSymbol],//
            valueType: ['', specialSymbol],//
        });
    }
    ngOnInit() {
        this.codeValues() //
        this.bussTypeList = this.code['bussType'];
        this.usageCategoryList = this.code['usageCategory'];
        this.statusIdList = this.code['statusIdtion'];
        this.pointerTypeList = this.code['pointerType'];
        this.queryClick();
        this.ispointerType();
    }
    //
    ispointerType() {
        if (this.infoform.value.pointerType == '1') {
            this.ispointer = true;
            this.nopointer = false;
        } else if (this.infoform.value.pointerType == '0') {
            this.ispointer = false;
            this.nopointer = true;
        }
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    // 
    showTime(param1, param2) {
        if (param1 == 'plan') {
            const startDateTime = this.commfunc.transDateN(this.userform.value.startDateTime);
            const endDateTime = this.commfunc.transDateN(this.userform.value.endDateTime);
            if (startDateTime && endDateTime) {
                if (startDateTime > endDateTime) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.userform.value.startDateTime = null;
                        } else {
                            this.userform.value.planContactTimeTo = null;
                        }
                    })
                }
            }
        }
    }
    // 
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }
    //
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['startDateTime'] = this.commfunc.transDateN(this.userform.value.startDateTime);
        param['endDateTime'] = this.commfunc.transDateN(this.userform.value.endDateTime);
        param['operationType'] = '3';
        this.httpService.subdibisionConfigMain(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    add() {
        this.isadd = true;
        this.isupdate = false;
        this.display = true;
        this.addUpHeader = '';
        this.reset1();
        this.ispointerType();
        this.infoform.get('endDateTime').setValue(this.dataDate);
        this.infoform.get('startDateTime').setValue(this.dataDate);
    }
    // 
    updatefast(col) {
        this.isadd = false;
        this.isupdate = true;
        this.display = true;
        this.addUpHeader = '';
        this.infoform.get('subdibisionId').setValue(col.subdibisionId);
        this.infoform.get('pointerType').setValue(this.userform.value.pointerType);
        this.infoform.get('endDateTime').setValue(col.endDateTime);
        this.infoform.get('startDateTime').setValue(col.startDateTime);
        // this.infoform.get('usageCategory').setValue(col.usageCategory);
        this.infoform.get('bussType').setValue(col.bussType);
        if (col.subdibisionName) {
            this.infoform.get('subdibisionName').setValue(col.subdibisionName);
        }
        if (this.userform.value.pointerType == '1') {
            this.infoform.get('valueType').setValue(col.valueType);
        }
        this.ispointerType();
    }
    //
    saveadd() {
        if (!this.userform.value.pointerType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return
        }
        if (!this.userform.value.bussType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return
        }
        const param = Object.assign({}, this.infoform.value);
        param['startDateTime'] = this.commfunc.transDateN(this.infoform.value.startDateTime);
        param['endDateTime'] = this.commfunc.transDateN(this.infoform.value.endDateTime);
        param['operationType'] = '0';
        this.httpService.subdibisionConfigMain(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.display = false;
                this.queryClick();
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    saveupdate() {
        const param = Object.assign({}, this.infoform.value);
        param['startDateTime'] = this.commfunc.transDateN(this.infoform.value.startDateTime);
        param['endDateTime'] = this.commfunc.transDateN(this.infoform.value.endDateTime);
        param['pointerType'] = this.infoform.value.pointerType;
        param['operationType'] = '1';
        this.httpService.subdibisionConfigMain(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.display = false;
                this.queryClick();
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    // 
    deletefast(col) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let param = {
                    subdibisionId: col.subdibisionId,
                    pointerType: this.userform.value.pointerType,
                    operationType: '2'
                };
                this.httpService.subdibisionConfigMain(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            },
            reject: () => {
            }
        });
    }
    // 
    reset() {
        this.userform.get('subdibisionId').setValue('');
        this.userform.get('subdibisionName').setValue('');
        this.userform.get('bussType').setValue('0');
        // this.userform.get('usageCategory').setValue('0');
        this.userform.get('pointerType').setValue('0');
        this.userform.get('startDateTime').setValue('');
        this.userform.get('endDateTime').setValue('');
    }
    reset1() {
        this.infoform.get('subdibisionName').setValue('');
        this.infoform.get('bussType').setValue('0');
        // this.userform.get('usageCategory').setValue('0');
        this.infoform.get('pointerType').setValue('0');
        this.infoform.get('startDateTime').setValue('');
        this.infoform.get('endDateTime').setValue('');
    }
    paginate(event) {
        //
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
    //
    back() {
        this.display = false;
    }
}